#granola() { .render { 
    .nav() {
    	#ui.nav();
        .g-nav {
            #ui.nav(nav-outer);
            
            & ul { 
            	#ui.nav(nav-list);
            }
            & li {
            	#ui.nav(nav-list-item);
            }
            & a {
            	#ui.nav(nav-list-link);
            }
            &-justify {
            	#ui.nav(nav-justify);
            }
            &-justify-item {
            	#ui.nav(nav-justify-item);
            }
        }
        .prefix("btn-", {
            display: inline-block;
        });
    }
    .type() {
    	#ui.type();
    	h1, .h1 {
    		#ui.type(h1);
    	}
    		
    }
	
}}
